.navigation {
  min-width: 66px;
  position: relative;

  &--openned {
    background-color: #283645;
    border-bottom: 1px solid #000000;
    box-sizing: border-box;
    margin-left: -130px;
    width: 100vw;
  }

  @media (min-width: $tablet-width) {
    &--openned {
      margin-left: -187px;
    }
  }

  @media (min-width: $desktop-width) {
    background-color: transparent;
    margin-right: 20px;
  }
}


.navigation__toggle {
  position: absolute;
  top: 0;
  right: 12px;
  z-index: 2;
  width: 50px;
  height: 24px;
  cursor: pointer;
  border: none;
  background-color: inherit;
  padding: 32px 33px;

  @media (min-width: $tablet-width) {
    padding: 43px 35px;
    left: -37px;
    top: 3px;
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.navigation--closed .navigation__toggle::before {
  content: "";
  position: absolute;
  width: 50px;
  height: 4px;
  top: 20px;
  left: 8px;
  border-radius: 1px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;

  @media (min-width: $tablet-width) {
    left: 33px;
    top: 40px;
  }
}

.navigation--closed .navigation__toggle {
  &:hover {
    &::before {
      background-color: $rare-color;
      box-shadow: 0 10px 0 0 $rare-color, 0 20px 0 0 $rare-color;
    }
  }

  &:active {
    &::before {
      background-color: $rare-color;
      box-shadow: 0 10px 0 0 $rare-color, 0 20px 0 0 $rare-color;
      opacity: 0.3;
    }
  }
}

.navigation--openned .navigation__list {
  display: flex;
  flex-direction: column;
  width: 100vw;
  margin-top: 66px;

  @media (min-width: $tablet-width) {
    margin-top: 110px;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    margin-top: 0;
    width: 620px;
  }
}

.navigation--openned .navigation__link {
  &:hover {
    color: $rare-color;
  }
}

.navigation--openned .navigation__toggle {
  right: 7px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    display: block;
    top: 30px;
    left: 26px;
    width: 30px;
    height: 2px;
    background-color: #ffffff;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:hover {
    &::before,
    &::after {
      background-color: $rare-color;
    }
  }

  &:active {
    &::before,
    &::after {
      background-color: $rare-color;
      opacity: 0.3;
    }
  }

  @media (min-width: $tablet-width) {
    left: 587px;
    top: 23px;
  }
}


.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: inherit;

  @media (min-width: $desktop-width) {
    height: 100%;
    align-items: center;
    width: 620px;
  }
}

.navigation--closed .navigation__list {
  display: none;

  @media (min-width: $desktop-width) {
    display: flex;
  }
}

.navigation__item {
  height: 64px;
  text-align: center;
  background-color: #283645;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  border-bottom: 1px solid #000000;
  box-sizing: border-box;

  @media (min-width: $desktop-width) {
    height: 43px;
    width: auto;
    border: none;
    background-color: transparent;

    &:not(:last-child) {
      margin-right: 79px;
    }

    &--current {
      border-bottom: 2px solid #ffffff;
    }
  }
}

.navigation__link {
  display: block;
  font-weight: bold;
  font-size: 18px;
  line-height: 32px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  padding-top: 15px;
  padding-bottom: 15px;

  &:hover {
    color: $rare-color;
  }

  &:active {
    color: #ffffff;
    opacity: 0.3;
  }

  @media (min-width: $desktop-width) {
    padding: 13px 0;
    font-size: 18px;
    line-height: 18px;
    font-weight: normal;
    font-style: normal;
    text-transform: none;
    width: auto;
  }
}
